import "./Register.css";
import ScrollTrigger from "react-scroll-trigger";

export default function Register(props) {
  const handleEnter = () => {
    if (props.currentColors) {
      props.setPreviousColors(props.currentColors);
    } else {
      props.setPreviousColors({
        top: "rgba(251,238,158, 1)",
        bottom: "rgb(255,251,230)",
      });
    }
    props.setCurrentColors({
      top: "rgba(251,238,158, 1)",
      bottom: "rgb(255,251,230)",
    });
    props.setCurrentSection("register");
  };

  return (
    <div className="Register h-full">
      <div className="max-w-screen-lg w-4/5 mx-auto z-10">
        <h1 className="font-bold text-left text-5xl pt-12">신청 안내</h1>

        <div className="max-w-screen-lg w-4/5 m-36 z-10 h-full text-xl flex flex-col" id="register-info">
          <div className="p-4"><strong>등록인원:</strong> 5명</div>
          <div className="p-4"><strong>등록기간:</strong> 5월 16일 까지</div>
          <div className="p-4">
            <strong>등록방법:</strong>  <br />
            <ol className="pl-8">
              <li>1) "신청하기"로 신청서 작성</li>
              <li>2) 국민은행 017001-04-026595 유경아</li>
            </ol>
          </div>
          <div className="p-4"><strong>문의:</strong> (010-8228-0513)</div>

          <div className="p-4"><strong>주관:</strong> CCC P2C Family Ministry</div>
          <button onclick={e => e.preventDefault()} className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-2 mt-4 rounded-full">
            <a href="https://docs.google.com/forms/d/e/1FAIpQLSf0dzqJFKJIRK5pRQyksPufD4ZskEntn38FuGtkW0xW3trnJQ/viewform?usp=sf_link">
              신청하기
            </a>
          </button>
        </div>

        <ScrollTrigger onEnter={handleEnter}>
          <div></div>
        </ScrollTrigger>
      </div>
    </div>
  );
}
