import CurriculumDesign from "../../Designs/CurriculumDesign";
import ScrollTrigger from "react-scroll-trigger";
import "./Curriculum.css";

export default function Curriculum(props) {
  const handleEnter = () => {
    if (props.currentColors) {
      props.setPreviousColors(props.currentColors);
    } else {
      props.setPreviousColors({ top: "#FCB045", bottom: "rgb(251,238,158)" });
    }
    props.setCurrentColors({ top: "#FCB045", bottom: "rgb(251,238,158)" });
    props.setCurrentSection("curriculum");
  };

  return (
    <div className="h-full Curriculum">
      <div className="max-w-screen-lg w-4/5 mx-auto z-10">
        <h1 className="font-bold text-right z-10 text-5xl mb-36 pt-12">강의 내용</h1>
        <div id="curriculum-content" className="flex flex-row h-full w-full">
          <div id="actual-content" className="w-full flex-col justify-center">
            <div className="h-full">
              <div className="p-12">제1강</div>
              <div className="p-12">Awesome</div>
              <div>
                <ScrollTrigger onEnter={handleEnter}></ScrollTrigger>
              </div>
              <div className="p-12">cool</div>
            </div>
          </div>
          <div className="svg-parent w-full">
            <svg id="killers">
              <CurriculumDesign
                height="1000"
                width="1000"
                x="100"
                y="0"
                className=""
              />
            </svg>
          </div>
        </div>
      </div>
    </div>
  );
}
