import MyParticles from "../../Designs/MyParticles";
import Bible from "../../Designs/Bible";
import "./Header.css";
import ScrollTrigger from 'react-scroll-trigger';

export default function Header(props) {
  const style = {
    height: "900px",
  };

  const handleEnter = () => {
    if (props.currentColors) {
      props.setPreviousColors(props.currentColors);
    } else {
      // props.setPreviousColors({top: '#999', bottom: '#191919'});
      props.setPreviousColors({top: "#3C3B6F", bottom: "#405eaa"});
    };
    props.setCurrentColors({top: "#3C3B6F", bottom: "#405eaa"});
    props.setCurrentSection("header");
  };

  return (
    <div>
      <MyParticles currentColors={props.currentColors} currentSection={props.currentSection} previousColors={props.previousColors} />
      <div className="SvgIcons absolute w-full h-screen">
        <svg id="workers">
          <Bible height="1000" width="1000" x="100" y="0" className="" />
        </svg>
      </div>
      <div
        id="heading"
        className="absolute w-full z-50 flex flex-row justify-between"
      >
        <div className="z-50 p-8" id="organization">
          <a href="">CCC경기 P2C FamilyLife</a>
        </div>
        <nav className="flex flex-row p-8 z-50">
          <div className="px-4 w-full z-50">
            <a href="#about">결혼학교</a>
          </div>
          <div className="px-4 w-full z-50">
            <a href="#speakers">강사소개</a>
          </div>
          <div className="px-4 w-full z-50">
            <a href="#curriculum">커리큘럼</a>
          </div>
          <div className="px-4 w-full z-50">
            <a href="#register">신청안내</a>
            <ScrollTrigger
              onEnter={handleEnter}
            ></ScrollTrigger>
          </div>
        </nav>
      </div>

      <div className="absolute flex flex-row w-full" id="Titles">
        <div className="w-full"></div>
        <div className="w-full flex flex-col">
          <div className="h-full"></div>
          <div className="h-full pt-12 text-right pr-36">
            <div className="pb-4 text-xl">크리스천 싱글들을 위한</div>
            <div className="pb-4 text-7xl">결혼학교</div>
            <div className="ButtonGroup flex flex-row justify-end">
              <button onclick={e => e.preventDefault()} className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-4 px-4 rounded-full">
                <a href="#register">신청하기</a>
              </button>
              <button onclick={e => e.preventDefault()} className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-4 px-4 ml-4 rounded-full">
                <a href="#about">알아보기</a>
              </button>
            </div>
          </div>
        </div>
      </div>
      <div style={style}></div>
    </div>
  );
}
